$dialog-desktop-width:     400px;

/*
 * <md-dialog class="pip-dialog" width="600">
 *     <div class="pip-header">
 *         <h3 class="m0">This is a Header</h3>
 *     </div>
 *     <div class="pip-body">
 *         <div class="pip-content">
 *         </div>
 *     </div>
 *     <div class="pip-footer">
 *         <div>
 *             <md-button ng-click="cancel()">Cancel</md-button>
 *             <md-button class="md-accent" ng-click="answer('ok')">Ok</md-button>
 *         </div>
 *     </div>
 * </md-dialog>
 */

.pip-dialog {
  @include flex();
  @include flex-direction(column);
  min-width: $dialog-desktop-width;
  width: $dialog-desktop-width;
  font: $font-body1;

  background-color: $color-content !important;

  @media (max-width: 450px) {
    min-width: calc(100% - 32px);
  }
}

.pip-dialog .pip-header {
  .pip-dialog-close {
    position: absolute;
    top: 10px;
    right: 10px;

    height: 32px;
    width: 32px;
    line-height: 32px;
    padding: 2px;

    background-color: transparent;
    box-shadow: none;
    border-radius: 50%;

    * {
      vertical-align: middle;
      font-size: 24px;
    }
  }
}